import React, { useState } from "react";
import "./Search.css";
import SearchIcon from "@material-ui/icons/Search";
import MicIcon from "@material-ui/icons/Mic";
import { Button } from "@material-ui/core";
import { useHistory } from "react-router-dom";
import { useStateValue } from "../StateProvider";
import { actionTypes } from "../reducer";
import AndroidRoundedIcon from "@material-ui/icons/AndroidRounded";
import AlarmRoundedIcon from "@material-ui/icons/AlarmRounded";
import AmpStoriesRoundedIcon from "@material-ui/icons/AmpStoriesRounded";
import AttachMoneyRoundedIcon from "@material-ui/icons/AttachMoneyRounded";
import AddRoundedIcon from "@material-ui/icons/AddRounded";
const Search = ({ hideButtons = false }) => {
  const [{}, dispatch] = useStateValue();

  const [input, setInput] = useState("");
  const history = useHistory();
  const search = (e) => {
    e.preventDefault();
    console.log("you searched for", input);
    dispatch({
      type: actionTypes.SET_SEARCH_TERM,
      term: input,
    });
    history.push("/search");
  };
  return (
    <form className="search">
      <div className="search-input">
        <SearchIcon className="search-input-icon" />
        <input
          value={input}
          onChange={(e) => setInput(e.target.value)}
          type="text"
          placeholder="search..."
        />
        <MicIcon />
      </div>

      {!hideButtons ? (
        <div className="search-buttons">
          <div className="button-and-desk-container">
            <Button type="submit" onClick={search}>
              <AndroidRoundedIcon />
            </Button>
            <p>Site 1</p>
          </div>
          <div className="button-and-desk-container">
            <Button variant="outlined">
              <AlarmRoundedIcon />
            </Button>
            <p>Site 2</p>
          </div>
          <div className="button-and-desk-container">
            <Button variant="outlined">
              <AmpStoriesRoundedIcon />
            </Button>
            <p>Site 3</p>
          </div>
          <div className="button-and-desk-container">
            <Button variant="outlined">
              <AttachMoneyRoundedIcon />
            </Button>
            <p>Site 4</p>
          </div>
          <div className="button-and-desk-container">
            <Button variant="outlined">
              <AddRoundedIcon />
            </Button>
            <p>Add shortcut</p>
          </div>
        </div>
      ) : (
        <div className="search-buttons">
          <Button
            className="search-buttons-hidden"
            type="submit"
            onClick={search}
          >
            Google Search
          </Button>
          <Button className="search-buttons-hidden" variant="outlined">
            Im feeling lucky
          </Button>
        </div>
      )}
    </form>
  );
};

export default Search;
